<template>
  <div class="p-timer w-60">
    <div class="p-timer__form mb-4">
      <p
        class="p-timer__guide alert alert-primary d-inline"
        v-show="!isFinished"
      >
        {{ unitTime }}
      </p>
      <div class="p-timer__time my-2">{{ formatTime }}</div>
      <button v-on:click="doStart" v-if="!isStarted" class="btn btn-primary">
        <i class="fas fa-caret-right mr-2"></i>Start
      </button>
      <button v-on:click="doStop" v-if="isStarted" class="btn btn-danger">
        <i class="fas fa-stop mr-2"></i>Stop
      </button>
    </div>
    <p class="p-timer__guide alert alert-primary" v-show="isFinished">
      <br />
      {{ unitTime }} 
    </p>
  </div>
</template>

<script>
import Push from "push.js";
export default {
  name: "CountdownTimer",
  props: {
    unitTime: { String },
  },
  data() {
    return {
      isStarted: false,
      isFinished: false,
      timerObj: null,
      min: 30,
      sec: 0,
    };
  },
  created() {
    this.min = this.unitTime;
    Push.Permission.request();
  },
  computed: {
    formatTime() {
      // 102
      // return (num < 10 ? "0" : "") + num;
      let timeStrings = [this.min.toString(), this.sec.toString()].map(
        function (str) {
          if (str.length < 2) {
            return "0" + str;
          } else {
            return str;
          }
        }
      );
      return timeStrings[0] + ":" + timeStrings[1];
    },
  },
  methods: {
    doStart() {
      let self = this;
      this.timerObj = setInterval(function () {
        self.countDown();
      }, 1000); // 1setIntervalObj
      this.isStarted = true;
      this.createPush();
    },
    doStop() {
      this.isStarted = false;
      clearInterval(this.timerObj);
    },
    countDown() {
      if (this.sec <= 0 && this.min >= 1) {
        this.min--;
        this.sec = 59; // 1059
      } else if (this.sec <= 0 && this.min <= 0) {
        this.finished(); // 0
      } else {
        this.sec--; // -1
      }
    },
    countUp() {
      if (this.sec >= 59) {
        this.min++;
        this.sec = 0; // 59
      } else {
        this.sec++; // +1(60)
      }
    },
    finished() {
      let self = this;
      // true
      this.isFinished = true;
      // clearInterval
      clearInterval(this.timerObj);
      // 
      this.createPush();

      // 
      this.min = this.unitTime;
      this.sec = 0;
      this.timerObj = setInterval(function () {
        self.countUp();
      }, 1000);
      this.isStarted = true;
    },
    createPush() {
      Push.create("", {
        body: this.unitTime + "",
        icon: "/images/icons-medal-50px.png",
        // timeout: 10000,
        requireInteraction: true, // 
        onClick: function () {
          // 
          window.focus();
          this.close();
        },
      });
    },
  },
};
</script>
